<template>
    <div class="padding-lr-20 order-list-page">
        <!--新建订单按钮-->
        <div class="" :class="addFlag !== '1'?'apply-create-btn-disabled':''">
            <div class="clearfix" style="padding:14px;margin-right:60px;">
                <div class="pull-right" style="width:294px;">
                    <Row :gutter="14">
                        <i-col span="12">
                            <div class="order-create-btn person-btn" @click="createOrder('1')">
                                <span class="iconfont icon-user"></span>
                                <span>新建个人订单</span></div>
                        </i-col>
                        <i-col span="12">
                            <div class="order-create-btn qy-btn" @click="createOrder('2')">
                                <span class="iconfont icon-building"></span>
                                <span>新建企业订单</span></div>
                        </i-col>
                    </Row>
                </div>
            </div>
        </div>
        <!--<div class="margin-b-20" :class="addFlag !== '1'?'apply-create-btn-disabled':''">-->
        <!--<Row :gutter="10">-->
        <!--<i-col span="12">-->
        <!--<div class="apply-create-btn person" @click="createOrder('1')">-->
        <!--<span class="iconfont icon-user"></span>-->
        <!--<span>新建个人订单</span></div>-->
        <!--</i-col>-->
        <!--<i-col span="12">-->
        <!--<div class="apply-create-btn  Qy" @click="createOrder('2')">-->
        <!--<span class="iconfont icon-building"></span>-->
        <!--<span>新建企业订单</span></div>-->
        <!--</i-col>-->
        <!--</Row>-->
        <!--</div>-->
        <div class="cm-panel" style="padding-top:10px;padding-bottom:10px;">
            <!--查询条件表单-->
            <i-form :model="formData" ref="formData" class="margin-b-10" :label-width="90">
                <div class="clearfix">
                    <div class="query-params-box">
                        <div class="query-params-con">
                            <Row>
                                <i-col :xs="12" :md="6">
                                    <Form-item label="订单号" prop="orderNo">
                                        <i-input type="text" clearable v-model="formData.orderNo" placeholder="请输入">
                                        </i-input>
                                    </Form-item>
                                </i-col>
                                <i-col :xs="12" :md="6">
                                    <Form-item label="姓名" prop="customerName">
                                        <i-input type="text" clearable v-model="formData.customerName"
                                                 placeholder="请输入">
                                        </i-input>
                                    </Form-item>
                                </i-col>
                                <i-col :xs="12" :md="6">
                                    <Form-item label="身份证号" prop="customerIdentityCard">
                                        <i-input type="text" clearable v-model="formData.customerIdentityCard"
                                                 placeholder="请输入">
                                        </i-input>
                                    </Form-item>
                                </i-col>
                                <i-col :xs="12" :md="6">
                                    <Form-item label="手机号" prop="customerPhone">
                                        <i-input type="text" clearable v-model="formData.customerPhone"
                                                 placeholder="请输入">
                                        </i-input>
                                    </Form-item>
                                </i-col>
                            </Row>
                            <div v-show="showCollapseQuery">
                                <Row>
                                    <i-col :xs="24" :md="12">
                                        <Form-item label="订单状态">
                                            <div class=" input-edit-table font0">
                                                <Form-item prop="stageCode" class="inline-formitem half-formitem">
                                                    <i-select type="text" v-model="formData.stageCode"
                                                              placeholder="请选择" @on-change="selectStage">
                                                        <i-option v-for="(item,index) in dp['stage']"
                                                                  :value="item.dictCode"
                                                                  :key="item.dictCode">
                                                            {{item.dictName}}
                                                        </i-option>
                                                    </i-select>
                                                </Form-item>
                                                <span class="formitem-split">-</span>
                                                <Form-item prop="statusCode" class="inline-formitem half-formitem">
                                                    <i-select type="text" v-model="formData.statusCode"
                                                              placeholder="请选择" :disabled="formData.stageCode ==''">
                                                        <i-option v-for="(item,index) in DICT_order_status"
                                                                  :value="item.dictCode" :key="item.dictCode">
                                                            {{item.dictName}}
                                                        </i-option>
                                                    </i-select>
                                                </Form-item>
                                            </div>
                                        </Form-item>
                                    </i-col>
                                    <i-col :xs="24" :md="12">
                                        <Form-item label="订单创建时间">
                                            <div class="input-edit-table font0">
                                                <Form-item prop="reportBeginDate" class="inline-formitem half-formitem">
                                                    <DatePicker type="date" v-model="formData.reportBeginDate"
                                                                :editable="false" placeholder="请选择"></DatePicker>
                                                </Form-item>
                                                <span class="formitem-split">-</span>
                                                <Form-item prop="reportEndDate" class="inline-formitem half-formitem">
                                                    <DatePicker type="date" v-model="formData.reportEndDate"
                                                                :editable="false" placeholder="请选择"></DatePicker>
                                                </Form-item>
                                            </div>
                                        </Form-item>
                                    </i-col>

                                    <i-col :xs="24" :md="12">
                                        <Form-item label="提报店名称" prop="dealerCode">
                                            <i-select type="text" v-model="formData.dealerCode"
                                                      placeholder="请选择">
                                                <i-option v-for="(item,index) in DICT_channelList"
                                                          :value="item.channelCode"
                                                          :key="item.channelCode">
                                                    {{item.channelName}}
                                                </i-option>
                                            </i-select>
                                        </Form-item>
                                    </i-col>
                                </Row>
                            </div>
                        </div>
                    </div>
                    <div class="more-btn-con">
                        <a class="more-btn" @click="toggleCollapseQuery">{{showCollapseQuery?'收起':'展开'}}
                            <Icon :type="showCollapseQuery?'ios-arrow-up':'ios-arrow-down'"></Icon>
                        </a>
                    </div>
                </div>

                <div class="text-center font0">
                    <i-button @click="fnReset('formData')">重置</i-button>
                    <i-button type="primary" @click="queryOrderList">查询</i-button>
                </div>

            </i-form>

            <!--查询列表结果-->
            <div class="ivu-tabs ivu-tabs-card visible-tabs">
                <div class="ivu-tabs-bar" style="margin-bottom:10px;">
                    <div class="ivu-tabs-nav-container">
                        <div class="ivu-tabs-nav-wrap" style="position: relative;">
                            <div class="ivu-tabs-nav-scroll">
                                <div class="nav-text ivu-tabs-nav">
                                    <template v-for="(item,index) in statsArr">
                                        <div @click="changeActiveTab(index)" class="ivu-tabs-tab"
                                             :class="activeTab===index?'ivu-tabs-tab-active':''">
                                            <div>
                                                <span>{{item.title}}</span>
                                                <number-ani-cell v-model="item.num"
                                                                 v-if="index>0"></number-ani-cell>
                                            </div>
                                        </div>
                                    </template>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="list-table-container">
                <i-table class="table-only-horizon-line" border
                         :columns="columns"
                         :data="tableListData"></i-table>
            </div>
            <Row class="margin-t-5">
                <Page :total="pager.total" :page-size="formData.Page" :current="formData.current"
                      :page-size-opts="pager.Opts"
                      @on-change="pageChange" @on-page-size-change="pageSizeChange" size="small" show-sizer
                      show-elevator></Page>
            </Row>
        </div>

        <div class="simple-breadcrumb col-xs-11">
            <span>进件管理</span>
            <Icon type="ios-arrow-forward"></Icon>
            <span>进件列表</span>
        </div>

        <!--1提回2取消3复议-->
        <Modal
                v-model="showOperModal"
                :mask-closable="false"
                footer-hide
                width="500"
                :title="modalObj[modalType] ? modalObj[modalType].title : '提示'">
            <p style="margin-top:-10px;">
                <i-input type="textarea" v-model="reconsiderReasonForm.reason" :rows="4"
                         :placeholder="modalObj[modalType] ? modalObj[modalType].placeholder : '请输入'"></i-input>
            </p>
            <div class="margin-t-30 text-right">
                <Button type="primary" :loading="submitLoading" @click="submitModal"
                        :disabled="!reconsiderReasonForm.reason">
                    {{modalType=='1'?'确 认':modalType=='2'?'确 认':modalType=='3'?'补充提报':''}}
                </Button>
            </div>
        </Modal>

        <!-- 发送短信待申请表电子签及担保人征信授权Modal -->
        <Modal 
            v-model="smsModalShow" 
            :mask-closable="false"
            footer-hide
            title="请确认重发短信"
            width="416">
            <Row>
                <CheckboxGroup v-model="smsType">
                    <i-col span="11" push="2"><Checkbox label="重发申请表短信"></Checkbox></i-col>
                    <i-col span="11" push="2"><Checkbox label="重发担保人短信"></Checkbox></i-col>
                </CheckboxGroup>
            </Row>
            <br/>
            <Row>
                <i-col push="14">
                    <Button 
                        type="default" 
                        @click="smsModalShow = false">取消</Button>
                    <Button type='primary' 
                        :disabled="smsType.length?false:true"
                        @click="smsTypefn">确定</Button>
                </i-col>
            </Row>
        </Modal>
    </div>
</template>

<script>
    import {
        mapState,
        mapGetters,
        mapMutations,
        mapActions
    } from 'vuex'

    import NumberAniCell from '@/components/apply/numberAniCell';
    import {yx} from "@/utils/helper";
    import eventHub from "@/utils/eventHub";
    import vFilter from '@/utils/filters';


    export default {
        name: "orderList",
        components: {
            NumberAniCell,
        },
        data() {
            return {
                account: this.$store.state.app.userInfo.userAccount,
                accountType: this.$store.state.app.userInfo.accountType,
                addFlag: this.$store.state.app.userInfo.addFlag,
                showCollapseQuery: false, //显示隐藏的查询条件
                pager: {
                    "total": 0,
                    "Opts": [10, 20, 50, 100, 200]
                },
                dp: {},
                DICT_order_status: [],//二级状态
                DICT_channelList: [],//提报店
                dateGroup: [],
                //进件管理页tab
                statsArr: [{
                    title: '全部',
                    key: 'all',
                    num: 0
                }, {
                    title: '待预审订单',
                    key: 'level11Count',//level11Count
                    messageType: 'PRE_AUDITORDER_COUNT',
                    num: 0
                }, {
                    title: '待提报订单',
                    key: 'level12Count',//level12Count
                    messageType: 'REPORTING_ORDER_COUNT',
                    num: 0
                }, {
                    title: '待签约订单',
                    key: 'level13Count',//level13Count
                    messageType: 'CONTRACT_ORDER_COUNT',
                    num: 0
                }, {
                    title: '待请款订单',
                    key: 'level14Count',//level14Count
                    messageType: 'PAYMENT_ORDER_COUNT',
                    num: 0
                }],
                formData: { //查询条件
                    "current": 1,
                    "rowCount": 10,
                    "orderNo": "",
                    "customerName": "",
                    "customerPhone": "",
                    "customerIdentityCard": "",
                    "stageCode": "", //状态一级
                    "statusCode": "", //状态二级
                    "subStageCode": "", //待办对应的状态
                    "orderStatus": "",
                    "dealerCode": "",
                    "reportBeginDate": "",
                    "reportEndDate": "",
                    "type": ""
                },
                activeTab: '',
                loading: false, //表格加载中
                exportLoading: false, //导出进行中
                columns: [
                    {
                        title: '序号',
                        width: 54,
                        align: 'left',
                        fixed: 'left',
                        render: (h, params) => {
                            return h('span', params.index + (this.formData.current - 1) * this.formData.rowCount + 1);
                        }
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 180,
                        align: 'left',
                        fixed: 'left',
                        render: this.renderOperBtn
                    },
                    {
                        title: '订单号',
                        key: 'orderNo',
                        width: 100,
                    },
                    {
                        title: '状态',
                        key: 'orderStatusDesc',
                        width: 120,
                        render: (h, params) => {
                            const desc = params.row.approvalRemark;
                            if (desc) {
                                return h('tooltip', {
                                    props: {
                                        //content: "拒绝原因：" + desc,
                                        placement: "bottom-start"
                                    },
                                }, [ //这个中括号表示是Tooltip标签的子标签
                                    params.row.orderStatusDesc, //表格列显示文字
                                    h('div', {
                                            slot: 'content',
                                            style: {
                                                whiteSpace: 'normal'
                                            }
                                        }, desc //整个的信息即气泡内文字
                                    )
                                ]);
                            } else {
                                return h('span', params.row.orderStatusDesc);
                            }
                        }
                    },
                    {
                        title: '客户名称',
                        key: 'customerName',
                        width: 240,
                        render: (h, params) => {
                            return h('div', [
                                h('div', params.row.customerName)
                            ]);
                        }
                    },
                    {
                        title: '手机号',
                        key: 'customerPhone',
                        width: 110,
                    },
                    {
                        title: '订单创建时间',
                        key: 'createTime',
                        width: 150,
                        render: (h, params) => {
                            if (!!params.row.createTime) {
                                var str = new Date(params.row.createTime).format('yyyy-MM-dd hh:mm:ss');
                                return h('div', str);
                            }
                            else return '';
                        }
                    },
                    {
                        title: '业务类型',
                        key: 'businessTypeDesc',
                        width: 90,
                    },
                    {
                        title: '产品方案名称',
                        key: 'productName',
                        width: 180,
                    },
                    {
                        title: '身份证号',
                        key: 'customerIdentityCard',
                        width: 160,
                    },
                    {
                        title: '车辆类型',
                        key: 'carTypeDesc',
                        width: 90,
                    },
                    {
                        title: '车型',
                        key: 'carStyleName',
                        width: 340,
                        render: (h, params) => {
                            var row = params.row;
                            if (!!row.carStyleName) {
                                var str = (row.carMasterBrandName || '') + ' ' + (row.carBrandName || '') + ' ' + (row.carModelName || '') + ' ' + (row.carStyleName || '');
                                return h('div', str);
                            }
                            else return '';
                        }
                    },
                    {
                        title: '提报店名称',
                        key: 'dealerName',
                        width: 240,
                    },
                    {
                        title: '订单类型',
                        key: 'customerTypeDesc',
                        width: 90,
                    },
                ],
                tableListData: [],
                close: false,
                sockjs: null,
                showOperModal: false,//显示1提回，2取消，3复议 三个弹窗
                reconsiderReasonForm: {
                    orderNo: '',//订单号
                    reason: '',//原因
                    rowDataIndex: undefined,//行索引
                },
                submitLoading: false, //三个弹窗中确定按钮加上Loading防止重复点击
                modalType: undefined, //1提回，2取消，3复议
                modalObj: {
                    '1': {
                        title: '订单提回',
                        txt: '提回',
                        placeholder: '请输入提回原因',
                        field: 'recallReason',
                        url: polarisserver + '/order/saveOrderRecall'
                    },
                    '2': {
                        title: '订单取消',
                        txt: '取消',
                        placeholder: '请输入取消原因',
                        field: 'cancelReason',
                        url: polarisserver + '/order/saveOrderCancel'
                    },
                    '3': {
                        title: '订单复议',
                        txt: '复议',
                        placeholder: '请输入复议原因',
                        field: 'reviewReason',
                        url: polarisserver + '/order/saveOrderPretrialReview'
                    }
                },
                smsModalShow:false,
                smsType:[], // 发送短信类型
                smsRow:{}
            }
        },
        computed: {},
        created() {
        },
        mounted() {
            this.queryDictData(['stage']).then(dp => {
                this.dp = dp;
            })
            document.title = '进件提报-进件列表';
            //请求四个待办
            this.queryUpcoming();
            //查询列表
            this.queryOrderList();
            //socket连接监听消息回传
            eventHub.$off('LEVEL_COUNT');
            eventHub.$on('LEVEL_COUNT', (response) => {
                this.acceptMessage(response);//已转换后的消息body
            })
        },
        //离开当前页面后执行
        destroyed() {
            eventHub.$off('LEVEL_COUNT');
        },
        methods: {
            ...mapMutations('app', [
                'showLoading',
                'hideLoading',
            ]),
            ...mapActions('dict', ['queryDictData']),
            //修改待办中的总数字
            updateStatsArr(obj) {
                if (!obj) return;
                this.statsArr.forEach((item, index) => {
                    let statsKey = item.key;
                    this.$set(item, 'num', obj[statsKey] || 0)
                })
            },
            //修改待办中的单个状态数字
            updateStatsNum(msg) {
                if (!msg) return;
                let obj = this.statsArr.find(item => item.messageType == msg.messageType)
                if (obj) {
                    obj.num = msg.extendValue.count;
                }
            },
            //处理websocket接收到的消息，已转换后的消息body
            acceptMessage(data) {
                let arr = ['PRE_AUDITORDER_COUNT', 'REPORTING_ORDER_COUNT', 'CONTRACT_ORDER_COUNT', 'PAYMENT_ORDER_COUNT']
                if (data && arr.indexOf(data.messageType) > -1) {
                    this.updateStatsNum(data)
                }
            },
            //选择状态一级
            selectStage() {
                let stage = this.formData.stageCode;
                if (!stage) this.DICT_order_status = [];
                else {
                    // this.showLoading();
                    this.$http.get(baseserver + "/dictionary/getNextDictionaryList/PO:" + stage, {}).then((res) => {
                        // this.hideLoading();
                        let result = res.data;
                        if (!result.success) return this.errorModal(result.errorMessage || '级联二级状态列表请求失败');
                        this.DICT_order_status = result.data || [];
                    }).catch((err) => {
                        // this.hideLoading();
                        this.errorCatchModal('级联二级状态列表请求出错：', err);
                    })
                }
            },
            //更多查询条件切换显示
            toggleCollapseQuery() {
                this.showCollapseQuery = !this.showCollapseQuery;
                //点击展开时再请求提报店列表
                if (this.DICT_channelList.length == 0) this.queryChannelList()
            },
            //切换待办的tab
            changeActiveTab(index) {
                if (this.activeTab === index) return false;
                let stage = index === 1 ? 'level11' : index === 2 ? 'level12' : index === 3 ? 'level13' : index === 4 ? 'level14' : '';
                //切换待办列表，先清空列表中的字段
                this.clearForm('formData');
                this.activeTab = index;
                if (index === 0) {
                    this.queryOrderList();
                    return;
                }
                this.formData.subStageCode = stage;
                this.formData.current = 1;
                this.formData.type = 'task';//待办传task类型
                this.queryList();
            },
            //请求提报店 就是渠道
            queryChannelList() {
                let account = this.account;
                this.showLoading();
                this.$http.post(polarisserver + '/channel/list',
                    {"account": account}).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    if (!result.success) return this.errorModal(result.errorMessage || '提报店列表请求失败');
                    this.DICT_channelList = result.data || [];
                }).catch((err) => {
                    this.hideLoading();
                    this.errorCatchModal('提报店列表请求出错：', err);
                })
            },
            //请求四个待办项，不要加载效果
            queryUpcoming() {
                this.$http.get(polarisserver + '/order/upcoming').then((res) => {
                    let result = res.data;
                    if (!result.success) return this.errorModal(result.errorMessage || '待办统计信息请求失败');
                    this.updateStatsArr(result.data);
                }).catch((err) => {
                    this.errorCatchModal('待办统计信息请求出错：', err);
                })
            },
            //点击查询按钮，为带查询条件的查询列表
            async queryOrderList() {
                let params = Object.assign({}, this.formData);
                if (params.reportEndDate && params.reportBeginDate && (new Date(params.reportEndDate).getTime() < new Date(params.reportBeginDate).getTime()))
                    return this.errorModal('查询结束日期不得早于开始日期');
                this.activeTab = 0;
                this.formData.subStageCode = '';
                this.formData.current = 1;
                this.formData.type = 'order';
                this.queryList();
            },
            //执行查询
            async queryList() {
                if (this.loading) return false;
                this.tableListData = [];
                //查询列表及统计状态数据
                let params = Object.assign({}, this.formData);
                if (params.reportBeginDate)
                    params.reportBeginDate = new Date(params.reportBeginDate).getTime();
                if (params.reportEndDate)
                    params.reportEndDate = new Date(new Date(params.reportEndDate).format('yyyy-MM-dd') + ' 23:59:59').getTime();
                this.showLoading()
                this.loading = true;
                this.$http.post(polarisserver + '/order/queryPage',
                    params).then((res) => {
                    this.hideLoading()
                    this.loading = false;
                    let result = res.data;
                    if (!result.success) return this.errorModal(result.errorMessage || '进件列表请求失败');
                    let rowData = result.data || {};
                    this.pager.total = rowData.total;
                    this.tableListData = rowData.data || [];
                }).catch((err) => {
                    this.hideLoading()
                    this.loading = false;
                    this.errorCatchModal('进件列表请求出错：', err);
                })
            },
            //切换路由，参数为params
            go(routename, params) {
                this.$router.push({
                    name: routename,
                    params: params
                })
            },
            //表格翻页
            pageChange(curPage) {
                this.formData.current = curPage;
                this.queryList();
            },
            //表格改修改每页显示数量
            pageSizeChange(rowCount) {
                this.formData.rowCount = rowCount;
                this.queryList();
            },
            //清除表单所有数据
            fnReset(name) {
                this.$refs[name].resetFields();
                this.queryOrderList();
            },
            //点击重置按钮后要触发查询
            clearForm(name) {
                this.$refs[name].resetFields();
            },
            //封装报错弹窗
            errorModal(content, onOk, okText) {
                let options = {
                    title: '提示',
                    content: content,
                    okText: okText || '确 定',
                };
                if (onOk) options.onOk = onOk;
                let errTimer = setTimeout(() => {
                    this.$Modal.error(options);
                    clearTimeout(errTimer);
                }, 200)
            },
            //编辑行
            edit(index) {
                let row = this.tableListData[index] || {};
                this.go('orderEdit', {
                    orderNo: row.orderNo,
                    customerType: row.customerType
                });
            },
            //新建订单-征信授权页，企业客户暂时也跳转，待定
            createOrder(type) {
                if (this.addFlag !== '1') {
                    return this.$Message.error('当前用户没有创建订单权限!');
                }
                let pageName = yx.isQy(type) ? 'orderPreaudit' : 'orderCredit';
                this.go(pageName, {
                    customerType: type,
                })
            },
            //1提回、2取消、3复议
            submitModal() {
                if (this.modalType === undefined) return false;
                //弹窗类型
                let {txt, placeholder, url, field} = this.modalObj[this.modalType];
                let modalType = this.modalType;
                if (!this.reconsiderReasonForm.reason) {
                    return this.$Message.error(txt)
                }
                this.submitLoading = true;
                this.showLoading();
                //关闭弹窗
                this.modalType = undefined;
                this.showOperModal = false;
                var params = {orderNo: this.reconsiderReasonForm.orderNo};
                params[field] = this.reconsiderReasonForm.reason;
                this.$http.post(url, params).then(res => {
                    this.hideLoading();
                    this.submitLoading = false;
                    let result = res.data;
                    if (!result.success)
                        return this.errorModal(result.errorMessage || txt + '请求失败');
                    this.$Message.success(txt + '成功');
                    //复议时跳转提报编辑页
                    if (modalType == '3')
                        this.edit(this.reconsiderReasonForm.rowDataIndex);
                    else this.queryOrderList();
                }).catch((err) => {
                    this.hideLoading();
                    this.submitLoading = false;
                    this.errorCatchModal(txt + '请求出错：', err);
                })
            },
            //重发短信
            sendSmsFn(row, phone) {
                let content ='请确认是否将授权短信发送至：'
                if(row.orderStatus === 'P088') content = '请确认是否将担保人征信授权短信发送至：';
                if(row.orderStatus === 'P085') content = '请确定是否将申请表短信发送至：';
                if(row.orderStatus === 'P082') {
                    this.smsType = []
                    this.smsModalShow = true
                    this.smsRow = {...row, ...{phone: phone}}
                    return
                }
                content += vFilter.mobile(phone)
                this.$Modal.confirm({
                    title: '重发短信',
                    content,
                    okText: '确认发送',
                    cancelText: '取消',
                    onOk: () => {
                        if (this.submitLoading) return false;
                        this.submitLoading = true;
                        this.showLoading();
                        //P088为担保人授权
                        var url, params;
                        if (row.orderStatus === 'P088') {
                            url = polarisserver + '/guarantorAuth/sendMutiShortMessage';
                            params = {
                                orderNo: row.orderNo,
                                typeCode: '22',// 22:担保人短连接  固定值
                                telephone: row.customerPhone
                            };
                        } else if(row.orderStatus === 'P085'){
                            url = polarisserver + '/guarantorAuth/sendMutiShortMessage';
                            params = {
                                orderNo: row.orderNo,
                                typeCode: '30',// 22:担保人短连接  固定值
                                telephone: row.customerPhone
                            };
                        }else {
                            url = polarisserver + '/credit/sendSms';
                            params = {
                                custTel: phone,
                                orderId: row.orderNo,
                                sysId: '2'
                            };
                        }
                        //调用重发短信请求接口
                        this.$http.post(url, params).then(res => {
                                this.hideLoading();
                                this.submitLoading = false;
                                let result = res.data;
                                if (!result.success)
                                    return this.errorModal(result.errorMessage || '发送短信请求失败');
                                this.$Message.success('发送短信成功');
                            }
                        ).catch((err) => {
                            this.hideLoading();
                            this.submitLoading = false;
                            this.errorCatchModal('发送短信请求出错：', err);
                        })
                    },
                })
            },
            // 重发短信 待申请表电子签及担保人征信授权
            smsTypefn(){
                this.smsModalShow = false
                if (this.submitLoading) return false;
                this.submitLoading = true;
                let typeCode
                if(this.smsType.length === 2){
                    typeCode = 10000 
                }else if(this.smsType.includes('重发申请表短信')){
                    typeCode = 30
                }else{
                    typeCode = 22
                }

                //调用重发短信请求接口
                this.$http.post(polarisserver + '/guarantorAuth/sendMutiShortMessage', {
                    orderNo: this.smsRow.orderNo,
                    typeCode,// 22:担保人短连接  固定值
                    telephone: this.smsRow.customerPhone
                }).then(res => {
                        this.hideLoading();
                        this.submitLoading = false;
                        let result = res.data;
                        if (!result.success)
                            return this.errorModal(result.errorMessage || '发送短信请求失败');
                        this.$Message.success('发送短信成功');
                    }
                ).catch((err) => {
                    this.hideLoading();
                    this.submitLoading = false;
                    this.errorCatchModal('发送短信请求出错：', err);
                })
            },
            //渲染操作按钮
            renderOperBtn(h, params) {
                ////0主管,2组长,1普通员工,3二网
                //kehucfy0103001 组长    kehucfy0103002 组员（普通员工）   kehuccwshanghai主管0  kehucfy0103001001二网销售 wangxingche内部员工不是二网销售
                let row = params.row;
                let code = row.orderStatus;
                let account = this.account;
                let accountType = this.accountType;
                if (['0', '1', '2', '3'].indexOf(accountType) == -1) return false;
                //分隔符样式
                let splitSpan = ((h) => {
                    return h('span', {
                        style: {
                            'margin': '0 10px',
                            color: '#D8D8D8 '
                        },
                    }, '|')
                })(h);
                //创建操作按钮
                const createBtn = (params, text, disabled, clickFn, style) => {
                    return h('a', {
                        props: {
                            disabled: true
                        },
                        style: style || {},
                        on: {
                            click(){
                                window.orderNo = params.row.orderNo
                                clickFn()
                            }
                        }
                    }, text)
                };
                //判断订单状态添加操作按钮
                let arr = [];
                const toViewArr = []; //基本上都可以查看的，跳转查看页面
                const notViewArr = ["P066", "P001", "P002", 'P050', 'P023']; //不能查看的
                const resendSmsArr = ["P001", "P088", "P085", "P082"]; //发送短信并弹窗提示结果 重发短信，担保人时发送前的手机号要请求后台接口获取
                const toPreauditArr = ["P002"]; //待补充预审(授权成功)状态时跳转预审  去预审
                const toNewOrderArr = ["P004", "P089"]; //待补充提报（预审通过后）后跳转填写提报信息 去提报，"P089"是待组长审核，二网销售看不到的
                const toEditOrderArr = ['P005', 'P006', 'P007', 'P086', 'P014', 'P083', 'P084', 'P090']; //各种退回店后面跳转去提报页修改  去修改
                const reconsiderArr = ["P050"]; //预审拒绝的弹窗填写复议原因，保存后跳转提报页修改，提交后转人工审核  复议
                const rejectArr = ['P008', 'P010', 'P012', 'P013', 'P016', 'P034', 'P037', 'P038', 'P044', 'P095', 'P096', 'P091', 'P025']; //  提回
                const cancelArr = ["P005", 'P006', 'P007', 'P008', 'P010', 'P011', 'P012', 'P013', 'P014', 'P015', 'P016', 'P017', 'P083', 'P018', 'P019', 'P033',
                    'P034', 'P037', 'P095', 'P096', 'P039', 'P040', 'P041', 'P042', 'P043', 'P094', 'P045', 'P051', 'P052', 'P053', 'P080']; //取消

                var isSelf = account === row.createId && accountType === row.accountType;
                var isZuzhang = accountType === '2' && row.accountType === '3';
                var isErwang = accountType === '3';
                //查看按钮
                if (notViewArr.indexOf(code) < 0) {//除去这些code  都可以有查看按钮
                    let toViewBtn = createBtn(params, '查看', false, () => {
                        let row = params.row;
                        this.go('orderView', {
                            orderNo: row.orderNo,
                            customerType: row.customerType
                        });
                    })
                    arr.push(toViewBtn)
                }
                if (this.addFlag != '1') return arr;//如果addFlag不为1时，只能有查看按钮
                //征信授权的短信发送失败时重发短信，本人可以，组长可以操作二网销售的。担保人时发送前的手机号要请求后台接口获取
                if (resendSmsArr.indexOf(code) >= 0 && (isSelf || isZuzhang)) {
                    let resendSmsBtn = createBtn(params, '重发短信', false, () => {
                        if (code === 'P088') {
                            this.showLoading();
                            let url = polarisserver + '/customer/guarantor/personal/query/' + row.orderNo;
                            //let url = polarisserver + '/customer/guarantor/query/' + row.orderNo;
                            this.$http.get(url).then(res => {
                                this.hideLoading();
                                let result = res.data;
                                if (!result.success || !result.data || !result.data.guarantorPhone)
                                    return this.errorModal(result.errorMessage || '担保人信息请求失败');
                                this.sendSmsFn(row, result.data.guarantorPhone)
                            }).catch((err) => {
                                this.hideLoading();
                                this.errorCatchModal('担保人信息请求出错：', err);
                            })
                        }
                        else {
                            this.sendSmsFn(row, row.customerPhone)
                        }
                    })
                    if (arr.length > 0)
                        arr.push(splitSpan);
                    arr.push(resendSmsBtn);
                }
                //待补充预审(征信授权成功)状态时跳转预审，本人可以，组长应该查不到
                if (toPreauditArr.indexOf(code) >= 0 && isSelf) {
                    let toPreauditBtn = createBtn(params, '去预审', false, () => {
                        this.$router.push({
                            name: 'orderPreaudit',
                            params: {
                                customerType: row.customerType
                            },
                            query: {
                                orderNo: row.orderNo,
                            }
                        })
                    })
                    if (arr.length > 0)
                        arr.push(splitSpan);
                    arr.push(toPreauditBtn);
                }
                //待补充提报（预审通过后）后跳转填写提报信息，本人可以，组长可以操作二网销售的
                //待组长审核   P089  ：去提报 ，二网销售不会看到  确认：去提报页之后可以进行修改及保存操作
                if (toNewOrderArr.indexOf(code) >= 0 && (isSelf || isZuzhang)) {
                    var canToOrder = (code === "P089" && isZuzhang) || ((code != "P089") && isSelf);
                    if (canToOrder) {
                        let toNewOrderBtn = createBtn(params, '去提报', false, () => {
                            this.edit(params.index)
                        })
                        if (arr.length > 0)
                            arr.push(splitSpan);
                        arr.push(toNewOrderBtn);
                    }
                }
                //各种退回店后面跳转修改提报信息，P090是复议待提报，二网可操作，其余的去修改操作不给二网
                if (toEditOrderArr.indexOf(code) >= 0 && (isSelf || isZuzhang)) {
                    if ((code === 'P090' && isErwang) || !isErwang) {
                        let toEditBtn = createBtn(params, '去修改', false, () => {
                            this.edit(params.index)
                        })
                        if (arr.length > 0)
                            arr.push(splitSpan);
                        arr.push(toEditBtn);
                    }
                }
                //预审拒绝时显示复议，保存复议原因后跳转提报页，提交后转人工审核，二网的放开
                if (reconsiderArr.indexOf(code) >= 0 && (isSelf || isZuzhang)) {
                    let reconsiderBtn = createBtn(params, '复议', false, () => {
                        this.openOperModal('3', params);
                    })
                    if (arr.length > 0)
                        arr.push(splitSpan);
                    arr.push(reconsiderBtn);
                }
                //提回  成功后刷新列表
                if (rejectArr.indexOf(code) >= 0 && (isSelf || isZuzhang) && !isErwang) {
                    let rejectBtn = createBtn(params, '提回', false, () => {
                        this.openOperModal('1', params);
                    })
                    if (arr.length > 0)
                        arr.push(splitSpan);
                    arr.push(rejectBtn);
                }
                //取消  成功后刷新列表
                if (cancelArr.indexOf(code) >= 0 && (isSelf || isZuzhang) && !isErwang) {
                    let rejectBtn = createBtn(params, '取消', false, () => {
                        this.openOperModal('2', params);
                    })
                    if (arr.length > 0)
                        arr.push(splitSpan);
                    arr.push(rejectBtn);
                }
                return h('div', {
                    props: {},
                    'class': 'none-padding oper-col',
                    slot: 'content'
                }, arr)
            },
            //打开弹窗，并清空数据
            openOperModal(type, params) {
                this.reconsiderReasonForm.orderNo = params.row.orderNo;
                this.reconsiderReasonForm.rowDataIndex = params.index;
                this.reconsiderReasonForm.reason = '';
                this.modalType = type;
                this.showOperModal = true;
            }
        },
        watch: {}
    }
</script>



